.transaction_history {
  margin-top: 20px;
  width: 320px;
  overflow: hidden;
  text-align: center;
  background-color: aqua;
}

.head {
  background-color: aquamarine;
  text-transform: uppercase;
  height: 40px;
  font-size: 15px;
}

.tbody {
  height: 30px;
  background-color: bisque;
}

.tbody:nth-child(even) {
  background-color: darkgray;
}

.tbody:hover {
  background-color: gray;
  color: white;
}
