.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  padding-top: 10px;
  border-radius: 10px;
  overflow: hidden;
  background-color: aquamarine;
}

.avatar {
  border-radius: 50%;
  width: 200px;
  background-color: powderblue;
}

.name {
  margin-top: 10px;
  margin-bottom: 10px;
  color: black;
  text-transform: uppercase;
  font-weight: bold;
}

.description {
  text-align: center;
  font-weight: 500;
  color: gray;
}

.stats {
  display: flex;
}
.item {
  width: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
}

.label {
  padding-bottom: 10px;
  color: rgb(88, 86, 83);
}
